import './style.sass';
import { OrderBlockSectionRow } from '../OrderBlock';
import { OrderInput } from '../OrderInput';
import { errorMessages, patternValues } from './helpers';

export const FullnameInputBlock = ({ errors }) => {
  return (
    <OrderBlockSectionRow>
      <OrderInput
        label=""
        type="text"
        placeholder=""
        name="last_name"
        autoComplete="off"
        isValid={!errors.last_name}
        required={errorMessages.required('')}
        pattern={{
          value: patternValues.onlyCyrillic,
          message: errorMessages.onlyCyrillic('', 'female'),
        }}
      />
      <OrderInput
        label=""
        type="text"
        placeholder=""
        name="first_name"
        autoComplete="off"
        isValid={!errors.first_name}
        required={errorMessages.required('')}
        pattern={{
          value: patternValues.onlyCyrillic,
          message: errorMessages.onlyCyrillic('', 'female'),
        }}
      />
      <OrderInput
        label=""
        type="text"
        placeholder=""
        name="patronymic"
        autoComplete="off"
        isValid={!errors.patronymic}
        pattern={{
          value: patternValues.onlyCyrillic,
          message: errorMessages.onlyCyrillic('', 'neuter'),
        }}
      />
    </OrderBlockSectionRow>
  );
};
